import React, { useState } from "react";
import "./QuestionCard.css";

const QuestionCard = ({
  question,
  questionNumber,
  totalQuestions,
  overallNumber,
  overallTotal,
  chapterNumber,
  chapterName,
  sectionIndex,
  sectionTotal,
  onAnswer,
  onBack,
  onSkip,
}) => {
  const [selected, setSelected] = useState("");

  const { text, type, options, section } = question;

  // Options ë°°ì—´ë¡œ ë³€í™˜ (ë¬¸ìì—´ì¸ ê²½ìš° ì²˜ë¦¬)
  const getOptionsArray = (opts) => {
    if (!opts) return [];
    if (Array.isArray(opts)) return opts;
    
    if (typeof opts === 'string') {
      // ì‰¼í‘œë¡œ êµ¬ë¶„: "Regular,Transfer,Exchange"
      if (opts.includes(',')) {
        return opts.split(',').map(o => o.trim()).filter(Boolean);
      }
      // íŒŒì´í”„ë¡œ êµ¬ë¶„: "Regular|Transfer|Exchange"
      if (opts.includes('|')) {
        return opts.split('|').map(o => o.trim()).filter(Boolean);
      }
      // ë‹¨ì¼ ê°’
      return [opts.trim()];
    }
    
    return [];
  };

  // Likert 5ì  ì²™ë„
  const likert = [
    "ğŸ˜ Strongly Agree",
    "ğŸ™‚ Agree",
    "ğŸ˜ Neutral",
    "ğŸ˜• Disagree",
    "ğŸ˜¡ Strongly Disagree",
  ];

  // ì˜µì…˜ ë°°ì—´ (íƒ€ì…ì— ë”°ë¼)
  const answerOptions = type === "likert" ? likert : getOptionsArray(options);

  return (
    <div className="question-card fade-in">
      
      {/* ìƒë‹¨ ì •ë³´ */}
      <div className="q-header">
        <div className="chapter-label">
          <span className="chapter-index">Section {chapterNumber}.</span>
          <span className="chapter-name">{chapterName}</span>
        </div>

        <div className="q-progress-group">
          <div className="q-progress-section">
            {sectionIndex} / {sectionTotal}
          </div>
        </div>
      </div>

      {/* ì§ˆë¬¸ í…ìŠ¤íŠ¸ */}
      <h2 className="q-text">{text}</h2>

      {/* Multi ì˜µì…˜ - ë²ˆí˜¸ ìˆìŒ */}
      {type === "multi" && (
        <div className="q-options">
          {answerOptions.map((opt, idx) => (
            <button
              key={idx}
              className={`q-option ${selected === opt ? "selected" : ""}`}
              onClick={() => setSelected(opt)}
            >
              <span className="option-number">{idx + 1}</span>
              <span className="option-text">{opt}</span>
            </button>
          ))}
        </div>
      )}

      {/* Likert ì˜µì…˜ - ë²ˆí˜¸ ì—†ìŒ */}
      {type === "likert" && (
        <div className="q-options">
          {answerOptions.map((opt, idx) => (
            <button
              key={idx}
              className={`q-option ${selected === opt ? "selected" : ""}`}
              onClick={() => setSelected(opt)}
            >
              {opt}
            </button>
          ))}
        </div>
      )}

      {/* ì£¼ê´€ì‹ */}
      {type === "text" && (
        <textarea
          className="q-textarea"
          placeholder="Write your answer..."
          value={selected}
          onChange={(e) => setSelected(e.target.value)}
        />
      )}

      {/* ë²„íŠ¼ ì˜ì—­ */}
      <div className="q-footer">

        {/* Back */}
        <button className="back-btn" onClick={onBack}>
          <span className="btn-icon">â†</span>
          <span className="btn-text">Back</span>
        </button>

        {/* Skip */}
        <button className="skip-btn" onClick={onSkip}>
          <span className="btn-icon">â­</span>
          <span className="btn-text">Skip</span>
        </button>

        {/* Next */}
        <button
          className={`next-btn ${selected ? "active" : ""}`}
          disabled={!selected}
          onClick={() => {
            if (!selected) return;
            onAnswer(selected);
            setSelected("");
          }}
        >
          <span className="btn-text">Next</span>
          <span className="btn-icon">â†’</span>
        </button>
      </div>
    </div>
  );
};

export default QuestionCard;
